import React, { Component } from 'react';
import styles from "./OrderForm.module.css";
import FormInput from "../../MuiComponents/FormInput";
import FormButton from "../../MuiComponents/FormButton";



const formValid = ({formErrors, ...rest}) => {
    let valid = true;

    Object.values(formErrors).forEach(val => {
        val.length > 0 && (valid = false);
    });

    Object.values(rest).forEach(val => {
        val === null && (valid = false)
    });

    return valid;
};

class OrderForm extends Component {
    constructor(props){
        super(props);

        this.state = {
            step: 1,
            error: false,
            fullName:null,
            daytimeTel:null,
            street:null,
            suite:null,
            city:null,
            country:null,
            zipCode:null,
            formErrors: {
                fullName:'',
                daytimeTel:'',
                street:'',
                city:'',
                country:'',
                zipCode:'',
            },


        };
        this.handleChange = this.handleChange.bind(this);
        this.submitStep1 = this.submitStep1.bind(this);
    }


    // componentDidMount() {
    // }
    //
    // componentDidUpdate(prevProps, prevState, snapshot) {
    //     console.log(this.state);
    //     console.log(this.state.step)
    // }

    handleChange(event) {
        const {name, value} = event.target;
       let formErrors = {...this.state.formErrors};

       switch (name) {
           case 'fullName' :
               formErrors.fullName =
                   value.length === 0
                       ? 'minimum 3 characters required'
                       : '';
               break;
           case 'daytimeTel' :
               formErrors.daytimeTel =
                   value.length === 0
                       ? 'minimum 3 characters required'
                       : '';
               break;
           case 'street' :
               formErrors.street =
                   value.length === 0
                       ? 'minimum 3 characters required'
                       : '';
               break;
           case 'city' :
               formErrors.city =
                   value.length === 0
                       ? 'minimum 3 characters required'
                       : '';
               break;
           case 'country' :
               formErrors.country =
                   value.length === 0
                       ? 'minimum 3 characters required'
                       : '';
               break;
           case 'zipCode' :
               formErrors.zipCode =
                   value.length === 0
                       ? 'minimum 3 characters required'
                       : '';
               break;
           default:
               break;
       }
       this.setState({
           formErrors, [name]:value
       })
    }


    submitStep1=(e)=>{
        e.preventDefault();

        if(formValid(this.state) ){

            this.setState({
                step: this.state.step + 1,
                error: false
            });
           console.log('ITS OK !')
        } else {
         this.setState({
             error:true
         })
        }
            console.log('there is an error !')
            console.log(this.state.formErrors)

    };


    render() {
        const {formErrors, error, fullName, daytimeTel, street, suite, city, country, zipCode} = this.state;
        return (
            <div>
                <div className={styles.wrapper}>

                    <div className={styles.steps}>
                        <p className={this.state.step === 1 ? styles.activStep : styles.disableStep}>Shipping</p>
                        <p className={this.state.step === 2 ? styles.activStep : styles.disableStep}>Billing</p>
                        <p className={this.state.step === 3 ? styles.activStep : styles.disableStep}>Payment</p>
                    </div>

                    {this.state.step === 1 &&

                         <form  className={styles.formSec}>
                    <h1>
                        Shipping info
                    </h1>
                    <fieldset>
                        <legend>Recepient</legend>
                        {formErrors.fullName.length > 0 && (
                            <span>
                                {formErrors.fullName}
                            </span>
                        )}
                        <FormInput
                            handleChange={this.handleChange}
                            name='fullName'
                            placeholder={'Full Name'}
                            // classname={formErrors.fullName.length > 0 || ? styles.errorInput : styles.userName}
                            classname={`${formErrors.fullName.length > 0 ? styles.errorInput : styles.userName} 
                            ${error && fullName === null ? styles.errorInput : styles.userName}`}
                        />

                        <div className={styles.phoneSec}>

                            <FormInput
                                handleChange={this.handleChange}
                                name='daytimeTel'
                                placeholder={'Daytime phone'}
                                classname={`${formErrors.daytimeTel.length > 0 ? styles.errorInput : styles.userPhone}
                                 ${error && daytimeTel === null ? styles.errorInput : styles.userPhone}`}
                            />
                            <p>
                                For delivery  questions only
                            </p>
                        </div>

                    </fieldset>

                    <fieldset>
                        <legend>Address</legend>
                        <FormInput
                            handleChange={this.handleChange}
                            name='street'
                            placeholder='Street Address'
                            // classname={this.state.error ? styles.errorInput : ''}
                            classname={`${formErrors.street.length > 0 ? styles.errorInput : ''}
                             ${error && street === null ? styles.errorInput : ''}`}
                        />

                        <FormInput
                            handleChange={this.handleChange}
                            name='suite'
                            placeholder='Apt, Suite, Bldg, Gate Code. (optional)'
                            // classname={this.state.error ? styles.errorInput : ''}

                        />

                        <FormInput
                            handleChange={this.handleChange}
                            name='city'
                            placeholder='City'
                            // classname={this.state.error ? styles.errorInput : ''}
                            classname={`${formErrors.city.length > 0 ? styles.errorInput : ''}
                            ${error && city === null? styles.errorInput : ''}`}
                        />

                        <div className={styles.country}>
                            <FormInput
                                handleChange={this.handleChange}
                                name='country'
                                placeholder='Country'
                                // classname={this.state.error ? styles.errorInputCountry : styles.inputCountry}
                                classname={`${formErrors.country.length > 0 ? styles.errorInputCountry : styles.inputCountry}
                                ${error && country === null ? styles.errorInputCountry : styles.inputCountry}`}
                            />

                            <FormInput
                                handleChange={this.handleChange}
                                name='zipCode'
                                placeholder={'ZIP'}
                                // classname={styles.userZip}
                                classname={`${formErrors.zipCode.length > 0 ? styles.errorUserZip : styles.userZip}
                                ${error && zipCode === null ? styles.errorUserZip : styles.userZip}`}
                            />
                        </div>
                    </fieldset>

                            <FormButton
                                handleClick={this.submitStep1}
                                name={'Continue'}
                                style={styles.continueBtn}
                            />

                  </form>
                    }

                    {this.state.step === 2 &&

                         <form>

                             <div className={styles.billingTitle}>
                                 <h1>
                                     Billing information
                                 </h1>
                                 <p>Same as shipping</p>
                             </div>

                        <fieldset>
                            <legend>Billing Contact</legend>
                            <FormInput
                                handleChange={this.handleChange}
                                name='cardFullName'
                                placeholder='Full Name'
                                classname={this.state.error ? styles.errorInput : styles.userName}
                            />

                            <FormInput
                                handleChange={this.handleChange}
                                name='email'
                                placeholder='Email Address'
                                classname={this.state.error ? styles.errorInput : styles.formInput}
                            />
                        </fieldset>

                        <fieldset>
                            <legend>Billing Address</legend>
                            <FormInput
                                handleChange={this.handleChange}
                                name='billingStreet'
                                placeholder='Street Address'
                                classname={this.state.error ? styles.errorInput : styles.formInput}
                            />

                            <FormInput
                                handleChange={this.handleChange}
                                name='billingSuite'
                                placeholder={'Apt, Suite, Bldg, Gate Code. (optional)'}
                                classname={this.state.error ? styles.errorInput : styles.formInput}
                            />

                            <FormInput
                                handleChange={this.handleChange}
                                name='billingCity'
                                placeholder='City'
                                classname={this.state.error ? styles.errorInput : styles.formInput}
                            />

                            <div className={styles.country}>
                                <FormInput
                                    handleChange={this.handleChange}
                                    name='billingCountry'
                                    placeholder='Country'
                                    classname={this.state.error ? styles.errorInputCountry : styles.inputCountry}
                                />

                                <FormInput
                                    handleChange={this.handleChange}
                                    name='billingZip'
                                    placeholder='ZIP'
                                    classname={styles.userZip}
                                />
                            </div>
                        </fieldset>
                             <FormButton
                                 handleClick={this.submitStep2}
                                 name='Continue'
                                 style={styles.continueBtn}
                             />
                        </form>
                    }

                    {this.state.step === 3 &&

                        <form>
                        <h1>
                            Payment
                        </h1>
                            <div className={styles.secureInf}>
                                <p>
                                    This is a secure 128-bit SSL encrypted payment
                                </p>
                            </div>
                        <fieldset>
                            <legend>Cardholder Name</legend>
                            <FormInput
                                placeholder='Name as it appears on your card'
                                classname={styles.formInput}
                            />
                        </fieldset>

                        <fieldset>
                            <legend>Card Number</legend>
                            <FormInput
                                placeholder='XXXX XXXX XXXX XXXX'
                            />

                            <div className={styles.cardInf}>
                                <FormInput
                                    label='Expire Date'
                                    placeholder='MM/YY'
                                    classname={styles.expDate}
                                />

                                <FormInput
                                    label='Security Code'
                                    classname={styles.secCode}
                                />
                            </div>
                        </fieldset>
                            <FormButton
                                name='Pay Securely'
                                style={styles.continueBtn}
                            />
                    </form>

                    }

                </div>
            </div>
        );
    }
}

export default OrderForm;